export default {
	data() {
		return {
			title:'',
			strings:''
		}
	},
	
	onLoad:function(e){
		console.log(e);
		uni.showLoading({
			title: '疯狂加载请骚等',
			mask: false
		});
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news/36kr/'+e.newsid,
			method: 'GET',
			data: {},
			success: res => {
				console.log(res);
				this.title=res.data.title;
				this.strings = res.data.content;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods: {
		
	}
}
